import logo from "./logo.svg";
import "./App.css";
import {
  LocomotiveScrollProvider,
  useLocomotiveScroll,
} from "react-locomotive-scroll";
import { useRef, useState } from "react";
import ReactAudioPlayer from "react-audio-player";
import { RandomReveal } from "react-random-reveal";
import bw1 from "./assets/bw1.gif";
import bw2 from "./assets/bw2.gif";

import bw3 from "./assets/conbw.gif";

function GifSection({ setPage }) {
  const { scroll } = useLocomotiveScroll();
  const gifRef = useRef(null);

  function scrollAndUpdate() {
    setPage();
    setTimeout(() => {
      scroll.scrollTo(0, { duration: 0, disableLerp: true });
    }, 1900);
    gifRef.current.style.width = "100%";
    gifRef.current.style.height = "100%";
    setTimeout(() => {
      gifRef.current.style.opacity = "0";
    }, 1000);
  }

  return (
    <div
      className="main p1"
      id="gif2"
      data-scroll-section
      style={{ opacity: 1 }}
    >
      <div className="gif" ref={gifRef}>
        <p data-scroll onClick={scrollAndUpdate}>
          ENGAGE IN THE CON
        </p>
      </div>
    </div>
  );
}

function App() {
  const containerRef = useRef(null);

  const [page, setPage] = useState("p1");

  function setPageTransition() {
    setTimeout(() => {
      setPage("p2");
    }, 2000);
  }

  return (
    <LocomotiveScrollProvider
      options={{
        smooth: true,
        // ... all available Locomotive Scroll instance options
      }}
      watch={
        [
          //..all the dependencies you want to watch to update the scroll.
          //  Basicaly, you would want to watch page/location changes
          //  For exemple, on Next.js you would want to watch properties like `router.asPath` (you may want to add more criterias if the instance should be update on locations with query parameters)
        ]
      }
      containerRef={containerRef}
    >
      <div data-scroll-container ref={containerRef}>
        <div style={{ display: "none" }}>
          <ReactAudioPlayer src="noise.mp3" autoPlay controls volume={0.05} />
        </div>
        {page == "p1" ? (
          <div className="p1">
            <div className="main" data-scroll-section id="sec1">
              <p>THE CON SOCIETY</p>
            </div>
            <GifSection setPage={setPageTransition} />
          </div>
        ) : (
          <div
            className="main oo flexmid"
            data-scroll-section
            style={{ opacity: 1 }}
          >
            <p>
              <RandomReveal
                isPlaying
                duration={3}
                revealDuration={0.5}
                characters="IMMERSE IN THE CON"
              />
            </p>
            <div className="bwwrap">
              <div className="bw">
                <img src={bw1}></img>
                <p>FOUNDING FATHERS</p>
              </div>
              <div className="bw">
                {" "}
                <img src={bw2}></img>
                <p>DIVE DEEP</p>
              </div>
              <div className="bw">
                {" "}
                <img src={bw3}></img>
                <p>CONS</p>
              </div>
            </div>
          </div>
        )}
      </div>
    </LocomotiveScrollProvider>
  );
}

export default App;
